<template>
<div>
  <el-card>
    <div slot="header">
      <test>
        <div>内容管理</div>
      </test>
    </div>
    <el-form size="small" label-width="80px">
      <el-form-item label="状态:">
        <el-radio-group v-model="content.status">
          <el-radio :label="null">全部</el-radio>
          <el-radio :label="0">草稿</el-radio>
          <el-radio :label="1">待审核</el-radio>
          <el-radio :label="2">审核通过</el-radio>
          <el-radio :label="3">审核失败</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="频道:">
        <my-channel v-model="content.channel_id"></my-channel>
      </el-form-item>
      <el-form-item label="日期:">
        <el-date-picker
          v-model="pubdata"
          value-format='yyyy-MM-dd'
          type="daterange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          @change="dateChenge"
        ></el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">筛选</el-button>
      </el-form-item>
    </el-form>
  </el-card>
  <el-card style="margin-top:20px">
    <el-table
      :data="tableData"
      style="width: 100%">
      <el-table-column
        label="封面">
        <template slot-scope="scope">
          <el-image
          style="width: 100px; height: 100px"
          :src="scope.row.cover.images[0]"
          fit="fill"></el-image>
        </template>
      </el-table-column>
       <el-table-column
        prop="title"
        label="标题">
      </el-table-column>
       <el-table-column
        label="状态">
        <template slot-scope="scope">
          <el-tag v-if="scope.row.status === 1">待审核</el-tag>
          <el-tag v-if="scope.row.status === 2" type="success">审核通过</el-tag>
          <el-tag v-if="scope.row.status === 0" type="info">草稿</el-tag>
          <el-tag v-if="scope.row.status === 3" type="warning">审核失败</el-tag>
          <el-tag v-if="scope.row.status === 4" type="danger">已删除</el-tag>
        </template>
      </el-table-column>

      <el-table-column
        prop="pubdate"
        label="发布时间">
      </el-table-column>
      <el-table-column
        label="操作">
        <template slot-scope="scope">
            <el-row>
            <el-button @click="edit_article(scope.row.id)" type="primary" icon="el-icon-edit" circle plain></el-button>
            <el-button @click="delete_article(scope.row.id)" type="danger" icon="el-icon-delete" circle plain></el-button>
          </el-row>
        </template>

      </el-table-column>
    </el-table>
    <el-pagination
      background
      :page='content.page'
      :page-size='per_page'
      layout="prev, pager, next"
      @current-change='currentPage'
      :total="total">
    </el-pagination>
  </el-card>
</div>
</template>

<script>
export default {
  data () {
    return {
      content: {
        status: null,
        channel_id: null,
        begin_pubdate: null,
        end_pubdate: null,
        page: 1
      },
      id: '',
      per_page: 0,
      pubdata: '',
      tableData: [],
      total: 0,
      channel: []
    }
  },
  created () {
    this.getArticles()
  },
  methods: {
    onSubmit () {
      if (this.content.channel_id === '') this.content.channel_id = null
      this.getArticles(this.content)
    },
    dateChenge () {
      if (this.pubdata) {
        this.content.begin_pubdate = this.pubdata[0]
        this.content.end_pubdate = this.pubdata[1]
        // console.log(this.content.begin_pubdate, this.content.end_pubdate)
      }
    },
    async getArticles () {
      const { data: { data } } = await this.$http.get('/articles', { params: this.content })
      this.tableData = data.results
      this.total = data.total_count
      this.per_page = data.per_page
      this.id = data.results.id
    },
    currentPage (newPage) {
      this.content.page = newPage
      this.getArticles()
    },
    edit_article (id) {
      this.$router.push({ path: '/publish', query: { id } })
    },
    async delete_article (id) {
      await this.$http.delete(`/articles/${id}`)
    }
  }
}
</script>

<style>
.text {
  font-size: 14px;
}

.item {
  margin-bottom: 18px;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}

.box-card {
  width: 480px;
}
</style>
